import React, {useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss'






let 박스 = styled.div`
    padding: 20px;
`;

let 제목 = styled.h4`
    font-size: 25px;
    color: ${ props => props.색상 }
`;

function Detail(props){


    let [alert, alert변경] = useState(true);
    let [inputtext, inputtext변경] = useState('');

    useEffect(()=>{
        let 타이머 = setTimeout( ()=>{
            alert변경(false);
        }, 2000 );
        
        return ()=>{ clearTimeout(타이머); }

    },[]);

    

    let { id } = useParams();
    let history = useHistory();
    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id;
    });

    let 찾은상품2 = ''; 
    
    for(let i = 0; i < props.shoes.length; i++){
        if(props.shoes[i].id == id){
            찾은상품2 = props.shoes[i];
        };
    };

    return(
      <div className="container">
          <박스>
            <제목 className="red">Detail</제목>
          </박스>
          { inputtext }
        <input onChange={ (e)=>{ inputtext변경(e.target.value) } }/>
          { 
            alert == true
            ? (<div className="my-alert2">
                <p>재고가 얼마 남지 않았습니다.</p>
               </div>)
            : null
          }
          

              <div className="row">
                <div className="col-md-6">
                  <img src={'https://codingapple1.github.io/shop/shoes'+ (찾은상품2.id + 1) +'.jpg'} width="100%"/>
                </div>
                <div className="col-md-6 mt-4">
                  <h4 className="pt-5">{찾은상품2.title}</h4>
                  <p>{찾은상품2.content}</p>
                  <p>{찾은상품2.price}원</p>
                    <Info 재고={props.재고}></Info>
                  <button className="btn btn-danger" onClick={ ()=>{

                    let 재고 = [...props.재고];
                    console.log(재고);
                    재고[0] = 재고[0]-1;
                    console.log(재고);

                    props.재고변경(재고)

                  } }>주문하기</button>
                  <button className="btn btn-danger" onClick={()=>{ 
                      history.goBack();
                   }}>뒤로가기</button>
                </div>
              </div>
          </div>
    )
  }

function Info (props){
  return(
    <p>재고: {props.재고[0]}</p>
  ) 
}

  
  export default Detail;